.container {
  margin: auto;
  width: 1200px; }

.container-fluid {
  width: 100%; }

.row {
  position: relative;
  margin-left: -5px;
  margin-right: -5px; }
  .row:after {
    content: "";
    display: block;
    clear: both; }

